.hamburger {
  display: none;
}

.icon {
  background-color: #000;
  cursor: pointer;
  display: inline-block;
  height: 4px;
  margin-top: 10px;
  position: relative;
  width: 24px;
}

.icon:before,
.icon:after {
  content: '';
  background-color: #000;
  height: 4px;
  left: 0;
  position: absolute;
  width: 24px;
  transition: all 0.2s ease-in-out;
}

.icon:before {
  top: -8px;
}

.icon:after {
  bottom: -8px;
}

.icon + nav {
  margin-top: 10px;
}

.hamburger ~ nav {
  position: absolute;
  right: -200px;
  opacity: 0;
  transition: all 0.2s;
  width: calc(100% - 20px);
  box-sizing: border-box;
}

.hamburger:checked ~ nav {
  right: 10px;
  opacity: 1;
}

.hamburger:checked + label span {
  background-color: transparent;
}

.hamburger:checked + label span:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 0;
}

.hamburger:checked + label span:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  bottom: 0;
}

ul {
  background: #eee;
  margin: 0;
  padding: 10px;
}

li {
  list-style: none;
  margin: 0;
  padding: 5px 0;
}

@media screen and (min-width: 768px) {
  .hamburger ~ nav {
    opacity: 1;
    position: relative;
    right: auto;
    width: auto;
  }
  .icon {
    display: none;
  }
  .icon + nav {
    margin-top: 0;
  }
  li {
    display: inline-block;
    padding: 5px 20px;
  }
}
