/* ---------- FONTAWESOME ---------- */
[class*="fontawesome-"]:before {
  font-family: 'FontAwesome', sans-serif;
}
/* ---------- GENERAL ---------- */
* {
  box-sizing: inherit;
}
input {
  background-image: none;
  border: none;
  font: inherit;
  margin: 0;
  padding: 0;
  transition: all .3s;
}
/* ---------- ALIGN ---------- */
.align {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
/* ---------- GRID ---------- */
.grid {
  padding-top: 180px;
  margin-left: auto;
  margin-right: auto;
  max-width: 90%;
  width: 400px;
}
/* ---------- LOGIN ---------- */
#login h2 {
  background: lightblue;
  border-radius: 20px 20px 0 0;
  color: #fff;
  font-size: 28px;
  padding: 20px 26px;
  margin: 0;
}
#login h2 span[class*="fontawesome-"] {
  margin-right: 14px;
}
#login fieldset {
  background: #fff;
  border-radius: 0 0 20px 20px;
  padding: 20px 26px;
  margin: 0;
  border: 0;
  box-shadow: 0 3px 5px 0px #2323;
}
#login fieldset p {
  color: #777;
  margin-bottom: 14px;
}
#login fieldset p:last-child {
  margin-bottom: 0;
}
#login fieldset input {
  border-radius: 3px;
}
#login fieldset input[type="text"],
#login fieldset input[type="password"] {
  background: #eee;
  color: #777;
  padding: 4px 10px;
  width: 100%;
  border: 0;
}
#login fieldset input[type="submit"] {
  background: #232323;
  color: #fff;
  display: block;
  margin: 0 auto;
  padding: 4px 0;
  cursor: pointer;
  width: 100px;
}
#login fieldset input[type="submit"]:hover {
  background: #fff;
  color: #232323;
  transition: .37s ease;
}
.invalid {
  box-shadow: #f95252 0 0 1px 1px;
}
.error {
  display: none;
}
.error-msg {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 700px;
  margin: auto;
  position: absolute;
  height: 100px;
  color: white;
  top: 20px;
  left: 0;
  right: 0;
  background: #f95252;
}
.error-msg strong {
  font-size: 16px;
}

.react-daterange-picker__wrapper input {
  padding: 0 !important;
  border: 0 !important;
  color: white !important;
}
.react-daterange-picker__inputGroup {
  display: flex !important;
  justify-content: space-evenly !important;
}
.react-daterange-picker__wrapper {
  background: black !important;
  border: 0 !important;
  color: white !important;
  border-radius: 10px !important;
  padding: 5px 10px !important;
}
.react-daterange-picker__button {
  background: rgba(255,255,255,0.9) !important;
  border-radius: 50% !important;
  display: none !important;
}
